import axios from 'axios';
import types from '../actionTypes';
import delay from 'delay';

export const submitForm = (fields) => {
  return async (dispatch) => {
    try{
      await axios.post('https://2i55mqe75i.execute-api.us-east-1.amazonaws.com/prod', fields);
      dispatch({
        type: types.submitFormSuccessResponse
      })
    }catch(error) {
      /*eslint no-console:0*/
      console.error(error)
    }
    await delay(1000);
    dispatch({
      type: types.resetFormStatus
    })
  }
};
